@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  font-size: 14px;
  line-height: 18px;
  font-weight: 300;
}

body.no-scroll {
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  box-sizing: border-box;
}

@media (min-width: 768px) {
  .container {
    padding-left: 24px;
    padding-right: 24px;
    width: 93%;
    max-width: 1390px;
  }
}
@media (min-width: 1390px) {
  .container {
    max-width: 1390px;
  }
}
@media (min-width: 1924px) {
  .container {
    width: 1924px;
    max-width: 1924px;
  }
}
.favorite {
  position: absolute;
  right: 20px;
  top: 20px;
  padding: 0;
  border: 0;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  z-index: 10;
  width: 27px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../img/favorite.svg");
}

.favorite--active, .favorite:active {
  background-image: url("../img/favorite--active.svg");
}

.header {
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 30;
  width: 100%;
  min-width: 320px;
  transition: 0.3s;
}

.header_colored {
  background: #ffffff;
  border-color: #dbdce0;
}

.main-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.main-nav__toggle {
  background: url("../img/burger-icon.svg") no-repeat left center/35px 25px transparent;
  display: none;
  padding: 0;
  border: none;
  transition: 0.2s;
  cursor: pointer;
  outline: 0;
}

.main-nav__toggle--active {
  background: url("../img/cross-icon.svg") no-repeat 10% center/25px 25px;
}

.main-nav__actions {
  margin-left: 44px;
}

.main-nav__search {
  margin-left: 23%;
}

@media (max-width: 1389px) {
  .main-nav__navigation {
    display: none;
    position: absolute;
    height: calc(100vh - 60px);
    position: absolute;
    bottom: -1px;
    left: 0;
    transform: translateY(100%);
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    opacity: 0;
    pointer-events: none;
    transition: 0.35s opacity;
    background-color: #f8f8f6;
    z-index: 1000;
  }
  .main-nav--opened .main-nav__navigation {
    display: flex;
    opacity: 1;
  }
  .main-nav__toggle {
    display: block;
    height: 35px;
    width: 25px;
    margin-left: 25px;
  }
}
@media (min-width: 1390px) {
  .main-nav {
    padding-top: 50px;
    padding-bottom: 20px;
  }
}
.navigation {
  list-style: none;
  margin: 0;
  padding-top: 30px;
  padding-bottom: 0;
  padding-left: 30px;
}

.navigation__item {
  margin-bottom: 20px;
}

.navigation__item:not(:last-child) {
  margin-right: 46px;
}

.navigation__link {
  position: relative;
  color: #000000;
  text-transform: uppercase;
}
.navigation__link:hover::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #000000;
}

@media (min-width: 1390px) {
  .navigation {
    list-style: none;
    margin: 0;
    padding-top: 0;
    padding-left: 0;
    display: flex;
    justify-content: flex-end;
    flex-grow: 1;
  }
  .navigation__item {
    margin-bottom: 0;
  }
}
.user-actions {
  list-style: none;
  margin-right: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
}

.user-actions__item:not(:first-child) {
  margin-left: 49px;
}

.user-actions__link {
  position: relative;
}
.user-actions__link:hover svg {
  opacity: 0.5;
}

.user-actions__link-content {
  position: absolute;
  bottom: 0;
  right: -5px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  font-size: 10px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
  background-color: #E1CDA6;
}

.search {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search__input {
  font-size: 0;
  width: 21px;
  height: 21px;
  padding-left: 0;
  padding-top: 0;
  padding-bottom: 0;
  border: none;
  background-color: transparent;
}

.search__btn {
  position: absolute;
  top: 0;
  left: 0;
  width: 20.9px;
  height: 20.1px;
  background-color: transparent;
  border: 0;
  background-image: url("../img/icon-search.svg");
  cursor: pointer;
}
.search__btn:hover {
  opacity: 0.5;
}

.notice__slide {
  width: 100%;
  height: auto;
  overflow: hidden;
  padding-top: 13%;
  border-radius: 3px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url("../img/slide1-mobile.jpg");
}

.notice__slider .slick-prev {
  left: auto;
  right: 188px;
  top: auto;
  bottom: 75px;
  background-color: #ffffff;
  width: 156px;
  height: 4px;
  z-index: 10;
}
.notice__slider .slick-prev::before {
  content: "предыдущий";
  position: absolute;
  top: -25px;
  left: 0;
  font-size: 14px;
  font-weight: 300;
  font-family: "Montserrat", "Arial", sans-serif;
  text-transform: uppercase;
}

.notice__slider .slick-next {
  right: 51px;
  top: auto;
  bottom: 75px;
  background-color: #000000;
  z-index: 10;
  width: 140px;
  height: 4px;
}
.notice__slider .slick-next::before {
  content: "следующий";
  position: absolute;
  top: -25px;
  right: 0;
  font-size: 14px;
  font-weight: 300;
  font-family: "Montserrat", "Arial", sans-serif;
  text-transform: uppercase;
}

.notice__slider .slick-dots {
  bottom: 25px;
}
.notice__slider .slick-dots button {
  height: 7px;
  width: 7px;
  padding: 0;
  background-color: #ffffff;
  border-radius: 50%;
  opacity: 0.75;
}

.notice__slider .slick-dots .slick-active button {
  background-color: #000000;
}

@media (min-width: 768px) {
  .notice__slide {
    height: auto;
    padding-top: 7.35%;
    background-image: url("../img/slide1.jpg");
  }
}
@media (min-width: 768px) {
  .notice__slide {
    background-position: 99% center;
  }
}
.objects__slider {
  position: relative;
}
.objects__slider::before {
  content: "";
  width: 100%;
  height: 108px;
  position: absolute;
  bottom: -47px;
  left: 0;
  background-image: url("../img/line.svg");
  background-repeat: repeat-x;
  z-index: 100;
}

.objects__slide {
  width: 250px;
}

@media (min-width: 768px) {
  .objects__slide {
    width: auto;
  }
}
.tabs__slide {
  padding-top: 30px;
  padding-bottom: 10px;
  width: 250px;
  padding-left: 17px;
  box-sizing: border-box;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .tabs__slide {
    width: 322px;
  }
}
.cards {
  padding-top: 85px;
}

.cards__slide {
  padding-left: 10px;
  padding-right: 10px;
  width: 414px;
}

.cards__wrap {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.cards__text {
  padding-left: 25px;
  margin-bottom: 20px;
}

.cards__link {
  color: #000000;
  text-transform: uppercase;
}

.cards__slider {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.cards__slider .slick-prev {
  left: auto;
  right: 132px;
  top: -50px;
  z-index: 100;
  background-image: url("../img/icon-arrow-prev.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.cards__slider .slick-prev::before {
  content: "";
}
.cards__slider .slick-prev:hover {
  opacity: 0.5;
}

.cards__slider .slick-next {
  right: 83px;
  top: -50px;
  z-index: 100;
  background-image: url("../img/icon-arrow-next.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.cards__slider .slick-next::before {
  content: "";
}
.cards__slider .slick-next:hover {
  opacity: 0.5;
}

.cards__title {
  display: block;
  margin: 0 auto 50px;
  max-width: 280px;
  font-size: 20px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}

@media (min-width: 768px) {
  .cards__wrap {
    flex-direction: row;
  }
  .cards__text {
    position: relative;
    width: 212px;
    margin-bottom: 0;
    padding-left: 0;
  }
  .cards__title {
    font-size: 24px;
    line-height: 31px;
    margin: 0 auto 70px;
  }
  .cards__slider .slick-prev {
    top: -70px;
  }
  .cards__slider .slick-next {
    top: -70px;
  }
  .cards__link {
    position: absolute;
    left: 55px;
    top: 72%;
    transform: rotate(-90deg);
  }
  .cards__link::before {
    content: "";
    position: absolute;
    width: 9px;
    height: 6px;
    top: 6px;
    right: -15px;
    background-image: url("../img/arrow-small.svg");
    transform: rotate(90deg);
  }
  .cards__slider {
    width: calc(100% - 212px);
  }
}
.card {
  position: relative;
  margin-bottom: 30px;
  padding-bottom: 10px;
}
.card:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
.card:hover .card__link {
  display: block;
}

.card--new::before {
  content: "Новинка";
  position: absolute;
  right: 0;
  bottom: 45px;
  width: 81px;
  height: 24px;
  font-size: 11px;
  line-height: 24px;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  background-color: #80BDEE;
}

.card__title {
  margin-top: 15px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 14px;
}

.card__price {
  margin: 0;
  padding: 0;
  display: inline-block;
}

.card__old-price {
  margin: 0;
  margin-right: 23px;
  padding: 0;
  display: inline-block;
  color: #AAC4E1;
  text-decoration: line-through;
}

.card__image {
  display: block;
  margin: 0 auto;
}

.card__link {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 75px;
  padding: 12px 0 0;
  background-color: #ffffff;
}

.card__option-list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 6px;
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.card__input-radio {
  display: none;
}

.card__input-radio + .card__radio {
  font-size: 12px;
  line-height: 22px;
  text-align: center;
  width: 28px;
  box-sizing: border-box;
  color: #000000;
}

.card__input-radio + .card__radio {
  position: relative;
  display: block;
  cursor: pointer;
}

.card__input-radio + .card__radio::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 22px;
  width: 30px;
  border-radius: 0;
}

.card__input-radio:checked + .card__radio::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 22px;
  width: 30px;
  border-radius: 0;
  border: 1px solid #303030;
}

.card__button {
  display: flex;
  align-items: center;
  margin: 0 auto;
  position: relative;
  text-align: center;
  background-color: #ffffff;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  overflow: hidden;
  z-index: 1;
  color: #000000;
}
.card__button svg {
  margin-left: 3px;
}

.card__option-item--no-presence .card__radio {
  color: rgba(0, 0, 0, 0.3);
}

.card__inner-slider .slick-dots {
  bottom: -53px;
  text-align: right;
}
.card__inner-slider .slick-dots button {
  height: 18px;
  width: 18px;
  padding: 0;
  border-radius: 50%;
}
.card__inner-slider .slick-dots li {
  margin: 0 2px;
}
.card__inner-slider .slick-dots li:first-child > button {
  background-color: #C4C4C4;
}
.card__inner-slider .slick-dots li:nth-child(2) > button {
  background-color: #E1CDA6;
}
.card__inner-slider .slick-dots li:last-child {
  margin-right: 20px;
}

.slick-dotted.card__inner-slider {
  margin-bottom: 0;
}